/* ============================================
   APP STYLES - Elegant Light Theme
   ============================================ */

.app {
  position: relative;
  min-height: 100vh;
}

/* Background Decorations */
.bg-decoration {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: -1;
  overflow: hidden;
}

.bg-blob {
  position: absolute;
  border-radius: 50%;
  filter: blur(80px);
  opacity: 0.3;
  animation: float 20s ease-in-out infinite;
}

.blob-1 {
  width: 600px;
  height: 600px;
  background: var(--gradient-primary);
  top: -200px;
  right: -200px;
  animation-delay: 0s;
}

.blob-2 {
  width: 400px;
  height: 400px;
  background: linear-gradient(135deg, var(--accent) 0%, var(--primary-light) 100%);
  bottom: 20%;
  left: -100px;
  animation-delay: -5s;
}

.blob-3 {
  width: 300px;
  height: 300px;
  background: linear-gradient(135deg, var(--primary-light) 0%, var(--secondary) 100%);
  bottom: -100px;
  right: 20%;
  animation-delay: -10s;
}

/* Main Content */
main {
  position: relative;
  z-index: 1;
}

/* Section Transitions */
section {
  position: relative;
}

#intro {
  min-height: 100vh;
  display: flex;
  align-items: center;
  background: var(--bg-primary);
}

#experience {
  background: var(--bg-tertiary);
  position: relative;
}

#education {
  background: var(--bg-secondary);
  position: relative;
}

#projects {
  background: var(--bg-primary);
  position: relative;
}

/* Responsive */
@media (max-width: 768px) {
  .bg-blob {
    filter: blur(60px);
    opacity: 0.2;
  }
  
  .blob-1 {
    width: 300px;
    height: 300px;
  }
  
  .blob-2 {
    width: 200px;
    height: 200px;
  }
  
  .blob-3 {
    width: 150px;
    height: 150px;
  }
}

